    #acc-menu {
      position: absolute;
      background-color: #f9f9f9;
      min-width: 130px;
      box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
      z-index: 1;
      
    }
    #acc-menu a {
      float: none;
      color: black;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
      text-align: center;
      font-size: 12px;
      
      width: 100px;  
    }
    .menuRow:hover{
        background-color: rgba(85, 85, 85, 0.164);
    }
    .logout{
        background: #700f38a4;
        
    }
    
    .RightNav:hover .btn-acc{
      cursor: pointer;
      
      }
.NavBar{
    display: grid;
  grid-template-columns:  9fr 1fr ;
  background-color: #051c2cd5;
  overflow: hidden;
    height: 50px;
    margin: 0;
    color: white;
}
.NavBar a{
    text-decoration: none;
    color: white;
}

.LeftNav a{
    display: inline-block;
    width: 40px;
    padding: 14px;
    text-align: center;
    font-size: 16px;
}
.RightNav a{
    display: block;
    width: 50px;
    padding: 14px;
    text-align: center;
    font-size: 16px;
}

.LeftNav a:hover{
  color: grey;
  cursor: pointer;
}
.RightNav a:hover{
    color: grey;
    cursor: pointer;
  }
.RightNav a{
    
    margin: auto;
}
.login-container{
    display: grid;
     grid-template-rows: 2fr 5fr 2fr;  
      height: 90vh; 
      }

.main{
 margin: 15px;
}


